import { mapState } from "vuex";
import { USER_API } from "@/api/client";

export default {
  name: "firendList",
  data() {
    return {
      active: 0,
      inviteNum: 0,
      helpNum: 0,
      inviteList: [],
    };
  },
  computed: {
    ...mapState(["local"]),
    isBitKeep() {
      return this.local.UA.isBitKeep;
    },
    locale() {
      return this.local.locale;
    },
  },
  mounted() {
    this.isBitKeep &&
      BitKeepInvoke.setTitle(
        this.$t("ActivityBlindbox.ActivityBlindboxList.friendList")
      );
    this.userInvite();
  },
  methods: {
    async userInvite() {
      let { data, status } = await USER_API.userInviteList({
        type: this.active + 1,
      });
      if (status == 1) {
        return this.$toast(data);
      }
      this.inviteList = data.list;
      console.log(this.inviteList.length);
      
      this.inviteNum = data.invite_number;
      this.helpNum = data.help_number;
    },
  },
};
